.header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 1px solid #ddd;
    padding: 10px;
    background: #E7E9EB
} 

.table{
    table-layout: fixed;
    width: 100%;   
}

.table tr{
    padding: 10px;
    font-size: 12px;
}

.table th , .table td{
    border: 1px solid #ddd;
    padding : 10px;
}

.dropdown{
    display: flex;
    align-items: center;
}

.dropdown li{
    list-style: none;
    padding: 10px;
    
}

.button{
    border-radius: 2px;
    padding: 2px 3px;
    background: #333;
    color: white;
    margin: 10px;
}

.pagination li{
    background: black;
    text-decoration: none;
    list-style: none;
}

.text {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4; /* number of lines to show */
    -webkit-box-orient: vertical;
  }

  .row{
    
  }

  .row:hover{
      cursor: pointer;
      transition: all .1s;
  }
  th, td {
    padding: 15px;
    text-align: left;
  }

  tr:nth-child(even) {background-color: #E7E9EB;}

  tbody tr:hover{
      background: #4CAF50;
      color: white;
  }